#preview-container {
    width: 18%;
    min-width: 250px;
}

#top-bar {
    height: 20%;
    font-size: calc(12px + 1.5vh);
    font-weight: bold;
    text-align: center;
    user-select: none;
}

#preview-display {
    font-size: calc(12px + 3vh);
    font-weight: bold;
    text-align: center;
}